import request from '../../util/request.js'
export default {
	data() {
		return {
			user: {
				username: "",
				password: ""
			}
		}
	},
	methods: {
		register: function() {
			uni.navigateTo({
				url: "../register/register"
			})
		},
		login() {
			var _this = this;
			let id;
			let opts = {
				url: '/login',
				//接口地址（写你自己的地址）
				method: 'post'
				//请求类型
			};
			let data = _this.user;
			request.baseRequest(opts, data).then(res => {
				// console.log(JSON.stringify(res));
				//打印请求返回的数据         
				uni.setStorage({
					key: 'hjr_token',
					data: "Bearer" + " " + res.data.token,
					success: function() {
						console.log("Bearer " + res.data.token);
					}
				});
				id = res.data.user.id;
				let ins = {
					url: '/customer/' + id,
					method: 'get'
				}
				request.TokenRequest(ins).then(res => {
					uni.setStorage({
						key: 'user',
						data: res.data
					});
				}).catch(error => {
					console.log(error)
				});
				let cartins = {
					url: '/cart/' + id,
					method: 'get'
				}
				request.TokenRequest(cartins).then(res => {
					console.log("/*-/*-/*-/*-/*-/*-/*-*/-*/-*/*-/-**/-*/-*/-*-")
					console.log(res.data)
					uni.setStorage({
						key: 'cart',
						data: res.data
					});
				}).catch(error => {
					console.log(error)
				});
				uni.showToast({
					title: res.message,
					position:'bottom',
					duration: 2000
				});
			}, error => {
				console.log("出错了");
				console.log(error);
			});



			uni.switchTab({
				url: "../index/index"
			})
		},
	}
}
